import React, { useState, Component } from 'react';
import ReactDOM from 'react-dom';
import Display from './display';
import SymbolExchangeInput from './input';
import Container from 'react-bootstrap/Container';
import Alert from 'react-bootstrap/Alert';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Badge from 'react-bootstrap/Badge';
import axios from 'axios';
import Toast from 'react-bootstrap/Toast';

require('dotenv').config();

class App extends Component {
   constructor(props) {
      super(props);
      this.state = { latestQuotes: [], "errorMessage": null, 'message': null }            
   }

   removeMessage = () => {
      this.setState({ ...this.state, "errorMessage": null, 'message': null });
   }

   getQuote = (symbol, exchange) => {
      //console.log("index.js - symbol: " + symbol);
      //console.log("index.js - exchange: " + exchange);
      const yahooSymbol = exchange === "tsx" ? symbol + ".to"
      : (exchange === "cse" ? symbol + ".cn" 
      : (exchange === "tsxv"? symbol + ".v" 
      : (exchange === "neo"? symbol + ".ne" : symbol)));
      
      let quote = null;       

      axios.get('https://enjoyit-cors-anywhere.herokuapp.com/https://query1.finance.yahoo.com/v7/finance/quote?formatted=true&symbols=' + yahooSymbol)
         .then(res => {
            console.log(res);
            const quotes = res.data.quoteResponse.result;
            console.log(quotes);
            if (quotes && quotes.length >  0) {
               //console.log("Got last price ... ");
               const message = quotes[0].longName ?  quotes[0].longName + ' on ' + quotes[0].fullExchangeName
                  : (quotes[0].shortName ? quotes[0].longName + ' on ' + quotes[0].fullExchangeName : null);
               console.log(message);
               if (message) {
                  this.setState({ ...this.state, "errorMessage": null, 'message': message });
                  quote = quotes[0];
               } else {
                  this.setState({ ...this.state, "errorMessage": "Sorry, no data found." });
               }               
            } else {
               this.setState({ ...this.state, "errorMessage": "Sorry, no data found." });
            }
         })
         .catch(err => {
            //console.log(err);
            let message;
            if (err.response && err.response.data) {
               // The request was made and the server responded with a status code
               // that falls out of the range of 2xx
               //console.log(err.response.data);
               //console.log(err.response.status);
               //console.log(err.response.headers);
               if (typeof err.response.data === 'string') {
                  message = err.response.data.includes('Cannot GET /quote') ? 'The API backend is not available. Please have a check.' : 'HTTP ' + err.response.status + ': ' + err.response.data;
               } else {
                  message = err.response.data.name + ": " + err.response.data.message;
               } 
               this.setState({ ...this.state, "errorMessage": message });
            } else {
               message = err.name + ' (' + err.code + '): ' + err.message;
               this.setState({ ...this.state, "errorMessage": message });
            }
         });      
      console.log(quote);
      if (quote) {
         this.setState({ latestQuotes: [quote, ...this.state.latestQuotes] });
      }
   }

   render() {
      console.log("How many quote? " + this.state.latestQuotes.length);
      return (
         <Container>
            <Navbar bg="dark" variant="dark" sticky="top" expand="sm">
               <Navbar.Brand>
                  <span role="img" aria-label="Stock">&#x1F4C8;</span> <a href="https://enjoyit.ca" title="go to enjoyit.ca">enjoyit.ca</a> <Badge variant="light">v{process.env.REACT_APP_VERSION}</Badge>
               </Navbar.Brand>
               <Navbar.Toggle aria-controls="responsive-navbar-nav" />
               <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
                  <Nav>
                  <Nav.Link href="mailto:feedback@enjoyit.ca" title="provide feedback to feedback@enjoyit.ca">Feedback</Nav.Link>
                  <Nav.Link href="mailto:donations@enjoyit.ca" title="eTransfer less than $10 to donations@enjoyit.ca">Donations</Nav.Link>
                  </Nav>   
               </Navbar.Collapse>
            </Navbar>            
            <Notes />
            <SymbolExchangeInput handleSubmit={this.getQuote} handleInputChange={this.removeMessage} />
            {this.state.message ? <div className='pb-3'><DisplayMessage msg={this.state.message}/></div>: null}
            {this.state.errorMessage ? <Alert variant='warning'>{this.state.errorMessage}</Alert> : null}
            {this.state.latestQuotes.length > 0 ? <Display data={this.state.latestQuotes} /> : null}
         </Container>
      );
   }
}

const Notes = () => {
   const [show, setShow] = useState(true);

   if (show) {
      return (
         <Alert variant="primary" dismissible onClose={() => setShow(false)}>
            <Alert.Heading>FREE Real-Time Quotes for North American Stocks</Alert.Heading>
            <p>For non-common stocks, Yahoo! Finance convention is used.</p>            
         </Alert>         
      )
   }
   return <></>;
};

const DisplayMessage = props => {
   const msg = props.msg;
   const [show, setShow] = useState(true);

   
   return (
      <Toast delay={3000} autohide show={show} onClose={() => setShow(false)}><Toast.Body>{msg}</Toast.Body></Toast>
   )
   
};

ReactDOM.render(<App />, document.getElementById('root'));
